<template>
    <div id="progressReportShow">
        <div class="showForm">
            <el-form ref="showForm" >
                <table class="form_table" :model="showForm">
                    <thead>
                        <tr><th colspan="4">江苏省环保科研课题进展情况表</th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>课题名称：</td>
                            <td>
                                <el-input
                                    v-model="showForm.subjectCategory"
                                    :disabled="true">
                                </el-input>
                            </td>
                            <td>课题编号：</td>
                            <td>
                                <el-input 
                                    v-model="showForm.projectNo"
                                    :disabled="true">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>承担单位：</td>
                            <td>
                                <el-input 
                                 v-model="showForm.contractStartTime" 
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td>填报日期：</td>
                            <td>
                                <el-input 
                                 v-model="showForm.contractEndTime" 
                                 :disabled="true"
                                 ></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>项目负责人：</td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjeceLeader" 
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td>项目负责人联系电话：</td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectLeaderPhone"
                                 :disabled="true">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>主要联系人:</td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectContact"
                                 :disabled="true">
                                </el-input>
                            </td>
                            <td>主要联系人联系电话：</td>
                            <td>
                                <el-input 
                                 v-model="showForm.subjectContactPhone" 
                                 :disabled="true"
                                >
                            </el-input>
                            </td>
                        </tr>
                        <tr style="height:50px;">
                            <td>进展情况：</td>
                            <td colspan="3">
                                <el-radio v-model="radio" label="1">超前</el-radio>
                                <el-radio v-model="radio" label="2">正常</el-radio>
                                <el-radio v-model="radio" label="3">滞后</el-radio>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>一、合同要求研发任务</h4>
                                <p>合同要求研发任务共{{ taskForm.length }}项，具体如下：</p>
                                <ol>
                                    <li v-for="(item,index) in taskForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>二、目前进展情况</h4>
                                <p>已完成研发任务{{ progressForm.dataList.length }}项，完成工作进度{{ progressForm.progress }}%。</p>
                                <ol>
                                    <li v-for="(item,index) in progressForm.dataList" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>三、使用经费情况</h4>
                                <ol style="list-style-type:none;margin-left:-15px;">
                                    <li>已到位课题总经费{{ fundsForm.num }}万元；已使用课题经费{{ fundsForm.num }}万元，占总经费{{ fundsForm.num }}%。</li>
                                    <li>其中：已使用省环保课题经费{{ fundsForm.num }}万元，占胜环保课题经费{{ fundsForm.num }}%。</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>四、课题实施中存在的主要问题（研究遇到的困难及技术、方案、人员等变动情况）</h4>
                                <ol>
                                    <li v-for="(item,index) in problemForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>五、下一步研发工作安排</h4>
                                <ol>
                                    <li v-for="(item,index) in planForm" :key="index">{{ item }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>六、课题预计完成时间</h4>
                                <ol>
                                    <li>合同约定结题时间：{{ timeForm.date }}</li>
                                    <li>
                                        能否按合同约定时间完成课题？
                                        <el-radio v-model="timeForm.radio" label="1">能</el-radio>
                                        <el-radio v-model="timeForm.radio" label="2">不能</el-radio>
                                    </li>
                                    <li>预计申请课题验收时间：{{ timeForm.date }}</li>
                                </ol>
                            </td>
                        </tr>
                        <tr class="item">
                            <td colspan="4">
                                <h4>七、单位审核意见</h4>
                                <el-input 
                                    v-model="showForm.reason_basis" 
                                    :disabled="true"
                                    :autosize="{ minRows: 10}"
                                    type="textarea">
                                </el-input>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <div class="btn_group">
                <el-button @click="handleBack">返回</el-button>
            </div>
            
        </div>
    </div>
</template>

<script>
    export default {
        name:'progressReportShow',
        data(){
            return{
                radio: '',
                showForm: {
                },
                taskForm: [
                    '第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条第一条',
                    '第二条',
                    '第二条第三条第三条第三条第三条'
                ],
                fundsForm: {
                    num: 50
                },
                progressForm: {
                    progress: '60',
                    dataList: [
                        '进展一进展一进展一进展一进展一',
                        '进展二进展二进展二进展二进展二进展二进展二',
                        '进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三进展三'
                    ]
                },
                problemForm: [
                    '问题一',
                    '问题二',
                    '问题三'
                ],
                planForm: [
                    '安排一',
                    '安排二',
                    '安排三'
                ],
                timeForm: {
                    date: "2019年8月24日",
                    radio: ''
                },
                paramsData: {
                    id: this.$route.params.id
                }
            }
        },
        methods:{
            handleBack() {
                this.$router.go(-1);//返回上一页
            }
        }
    }
</script>

<style lang="less">
    #progressReportShow{
        .showForm{
            table.form_table{
                min-width: 1000px;
                width: 65.4%;
                @media screen and ( max-width: 1600px ) {
                    width: 87.4%;
                }
                .el-radio {
                    margin: 0 50px;
                }
                .item {
                    td {
                        * {
                            text-align: left;  
                        }
                        h4 {
                            padding: 15px 0;
                        }
                        background-color: #fff !important;
                        padding: 0 10px;
                        ol {
                            padding-bottom: 5px;
                            li {
                                margin-left: 20px;
                                line-height: 1.8;
                            }
                        }
                    }
                    .el-textarea {
                        margin-bottom: 10px;
                    }
                }
            }
            .qianding{
                padding: 10px 0;
                span{
                    line-height: 30px;
                }
                .el-input{
                    width: auto;
                    .el-input__inner{
                        line-height: 30px;
                        height: 30px;
                    }
                }
            }
        }
        .btn_group {
            margin-top: 10px;
            padding-bottom: 30px;
        }
    }
</style>



